
Template.__checkName("no_rights");
Template["no_rights"] = new Template("Template.no_rights", (function() {
  var view = this;
  return HTML.DIV({
    "class": "grid-small grid-block dialog no-rights"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("link"));
  }, function() {
    return [ "\n      ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("link"));
      }
    }, Blaze.View("lookup:errorMessage", function() {
      return Spacebars.mustache(view.lookup("errorMessage"));
    })), "\n    " ];
  }, function() {
    return [ "\n      ", Blaze.View("lookup:errorMessage", function() {
      return Spacebars.mustache(view.lookup("errorMessage"));
    }), "\n    " ];
  }), "\n  ");
}));
