
Template.__checkName("footer_code");
Template["footer_code"] = new Template("Template.footer_code", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "footer-code ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("footerCode"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("markdown"), function() {
      return Blaze.View("lookup:footerCode", function() {
        return Spacebars.mustache(view.lookup("footerCode"));
      });
    }), "\n    " ];
  }, function() {
    return [ "\n      ", HTML.A({
      href: "http://www.intranet.edf.fr",
      target: "_blank"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "EDF Collaborative Tools");
    })), "\n    " ];
  }), "\n  ");
}));
