
Template.__checkName("mobile_nav");
Template["mobile_nav"] = new Template("Template.mobile_nav", (function() {
  var view = this;
  return HTML.DIV({
    "class": "mobile-nav dark-bg"
  }, "\n    ", HTML.DIV({
    "class": "mobile-nav-inner"
  }, "\n      ", HTML.UL({
    "class": "mobile-menu"
  }, "\n        ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("mobileNav"),
      moduleClass: Spacebars.call("mobile-menu-item")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n      "), "\n    "), "\n  ");
}));
