
Template.__checkName("admin_wrapper");
Template["admin_wrapper"] = new Template("Template.admin_wrapper", (function() {
  var view = this;
  return HTML.DIV({
    "class": "grid admin-wrapper"
  }, "\n    ", Spacebars.include(view.lookupTemplate("admin_menu")), "\n    ", HTML.DIV({
    "class": function() {
      return [ "admin-contents ", Spacebars.mustache(view.lookup("currentRouteName")), "-contents" ];
    }
  }, "\n      ", Blaze._TemplateWith(function() {
    return {
      template: Spacebars.call(view.lookup("admin"))
    };
  }, function() {
    return Spacebars.include(function() {
      return Spacebars.call(Template.__dynamic);
    });
  }), "\n    "), "\n  ");
}));
