
Template.__checkName("layout");
Template["layout"] = new Template("Template.layout", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("appIsReady"));
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("css")), "\n    ", HTML.DIV({
      "class": function() {
        return [ "outer-wrapper ", Spacebars.mustache(view.lookup("currentPage")), " ", Spacebars.mustache(view.lookup("navLayout")) ];
      }
    }, "\n      ", Spacebars.include(view.lookupTemplate("mobile_nav")), "\n      ", HTML.DIV({
      "class": function() {
        return [ "inner-wrapper template-", Spacebars.mustache(view.lookup("pageName")) ];
      }
    }, "\n        ", Spacebars.include(view.lookupTemplate("header")), "\n        ", Blaze._TemplateWith(function() {
      return {
        zone: Spacebars.call("hero")
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("modules"));
    }), "\n        ", HTML.DIV({
      "class": "content-wrapper"
    }, "\n          ", Blaze.If(function() {
      return Spacebars.call(view.lookup("notAllowed"));
    }, function() {
      return [ "\n            ", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(Spacebars.dot(view.lookup("notAllowed"), "template")),
          data: Spacebars.call(Spacebars.dot(view.lookup("notAllowed"), "data"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n          " ];
    }, function() {
      return [ "\n            ", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("adminMenu"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n            ", Blaze._TemplateWith(function() {
        return {
          zone: Spacebars.call("contentTop")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("modules"));
      }), "\n            ", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("main"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n            ", Blaze._TemplateWith(function() {
        return {
          zone: Spacebars.call("contentBottom")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("modules"));
      }), "\n          " ];
    }), "\n          ", Blaze._TemplateWith(function() {
      return {
        zone: Spacebars.call("footer")
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("modules"));
    }), "\n        "), "\n        ", HTML.DIV({
      "class": "overlay hidden"
    }), "\n      "), "\n      ", Blaze.View("lookup:extraCode", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("extraCode")));
    }), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "app-loading"
    }, Spacebars.include(view.lookupTemplate("loading"))), "\n  " ];
  });
}));
