
Template.__checkName("logo");
Template["logo"] = new Template("Template.logo", (function() {
  var view = this;
  return HTML.DIV({
    "class": "logo header-module"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("logoUrl"));
  }, function() {
    return [ "\n      ", HTML.H1({
      "class": "logo-image "
    }, "\n        ", HTML.A({
      href: "/"
    }, "\n          ", HTML.IMG({
      src: function() {
        return Spacebars.mustache(view.lookup("logoUrl"));
      },
      alt: function() {
        return Spacebars.mustache(view.lookup("getSetting"), "title", "Telescope");
      }
    }), "\n        "), "\n      "), "\n    " ];
  }, function() {
    return [ "\n      ", HTML.H1({
      "class": "logo-text"
    }, HTML.A({
      href: "/"
    }, Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "title", "Telescope");
    }))), "\n    " ];
  }), "\n    ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "tagline");
  }, function() {
    return [ "\n      ", HTML.H2({
      "class": "tagline"
    }, Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "tagline");
    })), "\n    " ];
  }), "\n  ");
}));
