
Template.__checkName("header");
Template["header"] = new Template("Template.header", (function() {
  var view = this;
  return HTML.HEADER({
    "class": function() {
      return [ "header ", Spacebars.mustache(view.lookup("headerClass")) ];
    }
  }, "\n\n    ", HTML.A({
    href: "#menu",
    "class": "mobile-only mobile-menu-button button"
  }, "\n      ", Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "menu"));
  }), "\n    "), "\n\n    ", Spacebars.include(view.lookupTemplate("logo")), "\n\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasPrimaryNav"));
  }, function() {
    return [ "\n      ", Blaze._TemplateWith(function() {
      return {
        zone: Spacebars.call("primaryNav"),
        zoneClass: Spacebars.call("nav primary-nav desktop-nav header-module desktop-only"),
        moduleClass: Spacebars.call("header-submodule")
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("modules"));
    }), "\n    " ];
  }), "\n\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasSecondaryNav"));
  }, function() {
    return [ "\n      ", Blaze._TemplateWith(function() {
      return {
        zone: Spacebars.call("secondaryNav"),
        zoneClass: Spacebars.call("nav secondary-nav desktop-nav header-module desktop-only"),
        moduleClass: Spacebars.call("header-submodule")
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("modules"));
    }), "\n    " ];
  }), "\n\n  ");
}));
