
Template.__checkName("submit_button");
Template["submit_button"] = new Template("Template.submit_button", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "submit-button ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.A({
    id: "submit",
    "class": "submit btn btn-primary",
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "postSubmit");
    }
  }, Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "post");
  })), "\n  ");
}));
