
Template.__checkName("css");
Template["css"] = new Template("Template.css", (function() {
  var view = this;
  return [ HTML.STYLE("\n\n      ", Blaze.If(function() {
    return Spacebars.call(view.lookup("elementColors"));
  }, function() {
    return [ "\n        ", Blaze.View("lookup:elementColors", function() {
      return Spacebars.mustache(view.lookup("elementColors"));
    }), "\n      " ];
  }), "\n\n      ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "fontFamily");
  }, function() {
    return [ '\n        body, textarea, input, button, input[type="submit"], input[type="button"]{\n          font-family: ', Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "fontFamily");
    }), ";\n        }\n      " ];
  }), "\n\n      ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "backgroundCSS");
  }, function() {
    return [ "\n        body{\n          background: ", Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "backgroundCSS");
    }), ";\n        }\n      " ];
  }), "\n\n\n      .logo-image a{\n        ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "logoHeight");
  }, function() {
    return [ "\n          max-height:", Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "logoHeight");
    }), "px;\n        " ];
  }), "\n        ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "logoWidth");
  }, function() {
    return [ "\n          max-width:", Blaze.View("lookup:getSetting", function() {
      return Spacebars.mustache(view.lookup("getSetting"), "logoWidth");
    }), "px;\n        " ];
  }), "\n      }\n\n      ", Blaze.View("lookup:hideAuthClass", function() {
    return Spacebars.mustache(view.lookup("hideAuthClass"));
  }), "\n\n      @media screen and (max-width: 600px) {\n        /* find a way to get rid of this */\n        .post-discuss .icon, .post-discuss .action-count{\n          color: ", Blaze.View("lookup:getSetting", function() {
    return Spacebars.mustache(view.lookup("getSetting"), "buttonColor");
  }), ";\n        }\n      }\n\n      ", Blaze.If(function() {
    return Spacebars.call(view.lookup("extraCSS"));
  }, function() {
    return [ "\n        ", Blaze.View("lookup:extraCSS", function() {
      return Spacebars.mustache(view.lookup("extraCSS"));
    }), "\n      " ];
  }), "\n  \n  "), "\n  ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSetting"), "fontUrl");
  }, function() {
    return [ "\n    ", HTML.LINK({
      href: function() {
        return Spacebars.mustache(view.lookup("getSetting"), "fontUrl");
      },
      rel: "stylesheet",
      type: "text/css"
    }), "\n  " ];
  }) ];
}));
