
Template.__checkName("checker");
Template["checker"] = new Template("Template.checker", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("allow"));
  }, function() {
    return [ "\n    ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n  " ];
  }, function() {
    return [ "\n    ", Blaze._TemplateWith(function() {
      return {
        message: Spacebars.call(view.lookup("message"))
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("no_rights"));
    }), "\n  " ];
  });
}));
