
Template.__checkName("modules");
Template["modules"] = new Template("Template.modules", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("getClass"));
    },
    "data-zone": function() {
      return Spacebars.mustache(view.lookup("zone"));
    },
    id: function() {
      return Spacebars.mustache(view.lookup("getId"));
    }
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("getModules"));
  }, function() {
    return [ "\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("showModule"));
    }, function() {
      return [ "\n        ", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("template")),
          data: Spacebars.call(view.lookup("moduleData"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n      " ];
    }), "\n    " ];
  }), "\n  ");
}));
