
Template.__checkName("loader");
Template["loader"] = new Template("Template.loader", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("ready"));
  }, function() {
    return [ "\n    ", Blaze._InOuterTemplateScope(view, function() {
      return Spacebars.include(function() {
        return Spacebars.call(view.templateContentBlock);
      });
    }), "\n  " ];
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  " ];
  });
}));
